import React, { Component } from 'react';
import { Helmet } from "react-helmet";
import SEO from "../components/seo"
import BlogList from '../components/blogList'
import importBlogs from '../utils/importBlogs';
import ChartList from '../components/chartList'
import importCharts from '../utils/importCharts';

class Home extends Component {
    constructor(props) {
        super(props);
        this.state = {
            blogs: [],
            parallaxLoaded: false,
            charts: []
        }
    }

    componentDidMount() {
        importBlogs((blog) => {
            this.setState((state) => { return { blogs: state.blogs.concat([blog]) } })
        });
        importCharts((chart) => {
            this.setState((state) => { return { charts: state.charts.concat([chart]) } })
        });
    }

    handleScriptInject = () => {
        if (typeof window !== `undefined`) {
            setTimeout(() => {
                this.setState({
                    parallaxLoaded: true
                }, () => {

                    var image = document.getElementById('vrc__homeImg');
                    new window.simpleParallax(image, {
                        scale: 1.8,
                        delay: .6,
                        transition: 'cubic-bezier(0,0,0,1)'
                    })

                })
            }, 50)
        }
    }

    render() {
        const { blogs, parallaxLoaded, charts } = this.state;
        return (
            <>
                <Helmet
                    script={[{ src: 'https://cdn.jsdelivr.net/npm/simple-parallax-js@5.2.0/dist/simpleParallax.min.js' }]}
                    // Helmet doesn't support `onload` in script objects so we have to hack in our own
                    onChangeClientState={(newState, addedTags) => this.handleScriptInject(addedTags)}
                />
                <SEO title="Home" />
                <div className="vrc__homeImgContainer">
                    {parallaxLoaded && <img src="../../home.jpg" id="vrc__homeImg" />}
                </div>
                <div className="container">
                    <br />
                    <h1>Welcome to VrC site.</h1>

                    <br />
                    <p>How you'll enjoy :)</p>
                    <hr />
                    <h2>Blogs</h2>
                    <BlogList blogs={blogs} />
                    <hr />
                    <h2>Charts</h2>
                    <ChartList charts={charts} />
                </div>
            </>
        )
    }
}

export default Home;